export function ClassStatusSwitch(value) {
    switch (value) {
        case 1:
            return <Tag color="#2db7f5">Sp m</Tag>
        case 2:
            return <Tag color="#87d068">ang m</Tag>
        case 3:
            return <Tag color="#f50">Kt thc</Tag>
        default:
            return null;
    }
}

